.main-nav {
  position: absolute;
  left: -20px;
  top: -20px;
  width: 220px;
  box-sizing: content-box;
  background: #FFFFFF;
  box-shadow: 20px 0 20px rgba(0, 0, 0, 0.03);
  display: flex;
  justify-content: flex-end;
  padding: 72px;
  z-index: 0;
  opacity: 0;
  visibility: hidden;
  transform: translateX(-100%);
  //transform: scale(.95);
  //transform-origin: 0 0;
  //outline: 1px solid green;
  transition: opacity .3s, visibility .3s, transform .3s;
  &.is-active {
    opacity: 1;
    visibility: visible;
    //transform: scale(1);
    transform: translateX(0);
  }
}

.main-nav-items {
  display: flex;
  flex-direction: column;
}

.main-nav-item {
  + .main-nav-item {
    margin-top: 24px;
  }
}

.main-nav-item--fav {
  border-bottom: 1px solid #e5e5e5;
  padding-bottom: 24px;
  a.main-nav-link {
    font-family: "OpenSans-SemiBold", sans-serif;
    display: inline-flex;
    align-items: center;
    svg {
      margin-left: 7px;
      color: red;
    }
  }
}

a.main-nav-link {
  font-size: 16px;
  line-height: 22px;
  color: $cBlack;
  text-decoration: none;
  border-bottom: 0;

  &:hover {
    color: $cRed;
    text-decoration: underline;
  }
}

@media (max-width: 414px) {
  .main-nav {
    width: 100vw;
    box-sizing: border-box;
    left: -35px;
    top: -32px;
    padding-left: 35px;
    padding-right: 35px;
    padding-top: 132px;
    justify-content: flex-start;
    box-shadow: 0px 20px 40px rgba(0, 0, 0, 0.15);

  }
  .main-nav-items {
    width: 100%;
  }

  .main-nav-item--fav {
    padding-bottom: 0;
  }

  .main-nav-item {
    border-bottom:  1px solid #e5e5e5;
  }

  .main-nav-item + .main-nav-item {
    margin-top: 0;
  }

  a.main-nav-link {
    display: block;
    padding-top: 16px;
    padding-bottom: 16px;
  }
}
