html { height: 100%; min-height: 100%; }

body { height: 100%; background: #fff; font-size: 14px; min-width: 320px; line-height: 19px; display: flex; min-height: 100vh; font-family: 'OpenSans-Regular', sans-serif; color: #414141; }

.site-wrapper { display: flex; flex-direction: column; align-items: stretch; min-height: 600px; width: 100%; }

.wrapper { max-width: 1200px; padding-left: 35px; padding-right: 35px; margin-left: auto; margin-right: auto; box-sizing: border-box; }

.wrapper--rel { position: relative; }

.site-inner { flex-grow: 1; }

.icon { display: block; }

.text-common ul, .text-common ol { font-size: 16px; line-height: 22px; }

.text-common ul li + li, .text-common ol li + li { margin-top: 22px; }

.text-common li > ul, .text-common li > ol { margin-top: 22px; }

.text-common ul { list-style-type: none; }

.text-common ul li { padding-left: 28px; position: relative; }

.text-common ul li:before { content: ''; width: 6px; height: 6px; border-radius: 50%; background: #ff0000; position: absolute; left: 0; top: 8px; }

.text-common ol { list-style-type: none; counter-reset: ol1; }

.text-common ol li { counter-increment: ol1; }

.text-common ol li:before { content: counter(ol1) "."; margin-right: 8px; color: #ff0000; }

.text-common ol li > ol { counter-reset: ol2; }

.text-common ol li > ol li { counter-increment: ol2; padding-left: 12px; }

.text-common ol li > ol li:before { content: counter(ol1) "." counter(ol2) "."; color: #ff0000; margin-right: 8px; }

.text-image { display: flex; align-items: flex-start; margin-top: 40px; margin-bottom: 40px; }

.text-image img { max-width: 480px; flex-shrink: 0; margin-left: 20px; border-radius: 10px; }

.text-image--left { flex-direction: row-reverse; }

.text-image--left img { margin-left: 0; margin-right: 20px; }

.cols { display: flex; align-items: flex-start; }

.cols2 .col { width: 50%; }

.cols4 .col { width: 25%; }

.col { margin-right: 10px; margin-left: 10px; }

.col:first-child { margin-left: 0; }

.col:last-child { margin-right: 0; }

.figure { width: 100%; display: flex; flex-direction: column; margin-top: 40px; margin-bottom: 40px; }

.figure figcaption { margin-top: 24px; font-family: "OpenSans-Light", sans-serif; font-size: 16px; line-height: 22px; color: #999999; }

.figure img { width: 100%; border-radius: 5px; }

.styled-table { display: inline-block; vertical-align: top; max-width: 100%; overflow-x: auto; -webkit-overflow-scrolling: touch; font-size: 16px; line-height: 22px; margin-bottom: 40px; }

.styled-table th { font-family: Helvetica-Light, sans-serif; font-size: 20px; line-height: 24px; color: #414141; font-weight: normal; padding-bottom: 24px; border-bottom: 1px solid #999999; padding-right: 80px; text-align: left; }

.styled-table tbody tr:nth-child(even) td { background: #f5f5f5; }

.styled-table td { padding-bottom: 24px; padding-top: 24px; padding-right: 80px; }

.header-with-info { display: flex; align-items: center; }

.header-with-info .button, .header-with-info .header-link, .header-with-info .download-doc { margin-left: auto; }

.header-bordered { display: flex; padding-bottom: 24px; border-bottom: 1px solid #f5f5f5; justify-content: space-between; flex-wrap: wrap; }

.header-info { font-size: 14px; font-family: OpenSans-Regular, sans-serif; line-height: 19px; display: flex; align-items: center; color: #999999; }

.header-text { margin-right: 24px; }

.header-icon { font-size: 20px; color: #ff0000; margin-right: 16px; }

.header-icon path { stroke: #c4c4c4; fill: transparent; transition: stroke .3s, fill .3s; }

.header-icon:hover { cursor: pointer; }

.header-icon:hover path { stroke: #ff0000; }

.header-icon.is-active path { stroke: #ff0000; fill: #ff0000; }

.loadmore-action { display: flex; justify-content: center; margin-top: 24px; margin-bottom: 24px; }

a.header-link { font-family: Helvetica-Light, sans-serif; font-size: 24px; line-height: 29px; color: #999999; border-bottom: 0; }

@media (max-width: 959px) { .wrapper { padding-left: 35px; padding-right: 35px; }
  .text-image img { width: 50%; }
  .header-with-info { flex-wrap: wrap; } }

@media (max-width: 700px) { .text-image { flex-direction: column; }
  .text-image img { margin-top: 32px; width: auto; max-width: 100%; margin-left: auto; margin-right: auto; } }

@media (max-width: 600px) { .cols { flex-direction: column; }
  .cols2 .col, .cols4 .col { width: 100%; }
  .col { margin-left: 0; margin-right: 0; margin-top: 10px; margin-bottom: 10px; width: 100%; } }

@media (max-width: 330px) { .wrapper { padding-left: 15px; padding-right: 15px; } }

.site-header { width: 100%; padding-top: 64px; padding-bottom: 64px; flex-grow: 0; flex-shrink: 0; position: relative; z-index: 1; }

.site-header-inner { width: 100%; display: flex; align-items: center; justify-content: flex-start; position: relative; height: 32px; padding-left: 107px; padding-right: 35px; }

a.logo { border-bottom: 0; }

.header-burger { position: absolute; left: 32px; top: 0; }

.burger-button { display: block; width: 32px; height: 32px; position: relative; z-index: 1; background-color: transparent; }

.burger-button:before, .burger-button:after { content: ""; width: 24px; height: 2px; position: absolute; left: 4px; background: black; transform-origin: 50% 50%; transition: transform 0.2s 0.2s, background-color .2s; }

.burger-button:before { top: 8px; }

.burger-button:after { bottom: 8px; }

.burger-button:hover:before, .burger-button:hover:after { background: #ff0000; }

.burger-button:hover i:before, .burger-button:hover i:after { background-color: #ff0000; }

.burger-button i:before, .burger-button i:after { content: ""; width: 24px; height: 2px; position: absolute; left: 4px; top: 15px; background-color: #414141; transform-origin: 50% 50%; transition: transform 0.3s, background-color .2s; }

.burger-button.is-active:before, .burger-button.is-active:after { transform: scaleX(0); transition: transform 0.2s, background-color .2s; }

.burger-button.is-active i:before { transform: rotate(45deg); transition: transform 0.3s 0.2s, background-color .2s; }

.burger-button.is-active i:after { transform: rotate(-45deg); transition: transform 0.3s 0.2s, background-color .2s; }

.header-userinfo { margin-left: auto; position: relative; }

.userinfo-trigger { font-size: 24px; display: flex; align-items: center; color: #414141; transition: color .3s; background: transparent; }

.userinfo-trigger:hover { color: #999999; }

@media (max-width: 959px) { .site-header-inner { padding-right: 35px; padding-left: 35px; }
