.main-nav { position: absolute; left: -20px; top: -20px; width: 220px; box-sizing: content-box; background: #FFFFFF; box-shadow: 20px 0 20px rgba(0, 0, 0, 0.03); display: flex; justify-content: flex-end; padding: 72px; z-index: 0; opacity: 0; visibility: hidden; transform: translateX(-100%); transition: opacity .3s, visibility .3s, transform .3s; }

.main-nav.is-active { opacity: 1; visibility: visible; transform: translateX(0); }

