.ads-categories, .ads-new {
  h3 {
    font-family: Helvetica-Light, sans-serif;
    color: #414141;
  }
}

.ads-categories-items {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  flex-wrap: wrap;
  .button {
    font-family: Helvetica-Light, sans-serif;
    font-size: 18px;
    line-height: 22px;
    color: #414141;
    &:hover, &.is-active {
      color: $cRed;
    }
  }
}

.ads-categories-item {
  margin-bottom: 24px;
  margin-right: 24px;
}

.ads-new {
  margin-top: 40px;
  margin-bottom: 96px;
}

.cards-list {
  display: flex;
  flex-wrap: wrap;
  margin-left: -10px;
  margin-right: -10px;
}

.cards-item {
  margin-left: 10px;
  margin-right: 10px;
  width: calc((100% - 60px) / 3);
  border: 1px solid $cLightGrey;
  display: flex;
  margin-bottom: 32px;
  border-radius: 10px 0 10px 0;
  overflow: hidden;
}

.card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.card-main {
}

.card-image {
  height: 230px;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
  }
}

.card-content {
  padding: 32px;
  display: flex;
  flex-direction: column;
}

.card-category {
  color: $cMediumGrey;
  margin-bottom: 8px;
  font-size: 16px;
  a {
    border-bottom: 0;
  }
}

.card-header {
  font-family: Helvetica-Light, sans-serif;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 24px;
  font-weight: normal;
  a {
    border-bottom: 0;
    &:hover {
      color: $cRed;
    }
  }
}

.card-descr {
  font-size: 14px;
  line-height: 24px;
  max-height: 96px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.card-footer {
  padding: 32px;
}

.card-price {
  font-family: Helvetica-Light, sans-serif;
  font-size: 32px;
  line-height: 39px;
  color: #414141;
}

.card-meta {
  margin-top: 32px;
  display: flex;
  align-items: center;
  a, time {
    color: $cDarkGrey;
    font-size: 14px;
    line-height: 19px;
  }
}

a.card-city {
  padding: 12px 16px;
  background: $cLightGrey;
  border-radius: 25px;
  border-bottom: 0;
  transition: color .3s, background .3s;
  &:hover {
    background-color: $cRed;
    color: #ffffff;
  }
}

.card-date {
  margin-left: 24px;
}

.a_d_s-filter {
  margin-bottom: 56px;
}

.a_d_s-item {
  display: flex;
  align-items: flex-start;
  margin-bottom: 96px;
}

.a_d_s-item-info {
  width: 100%;
  max-width: 780px;
  margin-right: 20px;
}

.a_d_s-item-carousel {
  height: 590px;
  background: $cLightGrey;
  margin-bottom: 48px;
}

.a_d_s-item-text {

}

.a_d_s-item-seller {
  display: flex;
  flex-direction: column;
  align-items: center;
  h3 {
    font-family: Helvetica-Light, sans-serif;
    color: #414141;
  }
}

.seller-card {
  margin-top: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 270px;
}

.seller-image {
  width: 140px;
  height: 140px;
  border-radius: 50%;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.seller-name {
  font-family: Helvetica-Light, sans-serif;
  font-size: 24px;
  line-height: 29px;
  margin-bottom: 8px;
  margin-top: 21px;
  color: #414141;
  text-align: center;
}

.seller-job {
  font-size: 16px;
  line-height: 22px;
  text-align: center;
}

.seller-dept {
  margin-top: 16px;
  padding-top: 16px;
  position: relative;
  font-size: 14px;
  line-height: 19px;
  color: $cDarkGrey;
  text-align: center;
  &:before {
    content: '';
    width: 70px;
    height: 1px;
    background: $cMediumGrey;
    position: absolute;
    left: 50%;
    margin-left: -35px;
    top: 0;
  }
}

.seller-contacts {
  margin-top: 48px;
}

.seller-row {
  display: flex;
  flex-direction: column;
  align-items: center;
  +.seller-row {
    margin-top: 40px;
  }
}

.seller-label {
  font-size: 14px;
  line-height: 19px;
  color: $cDarkGrey;
}

.seller-phone {
  margin-top: 8px;
  font-family: Helvetica-Light, sans-serif;
  font-size: 24px;
  line-height: 29px;
  color: #414141;
  a {
    border-bottom: 0;

  }
}

.seller-email {
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  a {
    color: $cRed;
    border-bottom-color: $cRed;
  }
}

.ads-similar {

}

@media (max-width: 960px) {
  .ads-categories-item {
    margin-bottom: 8px;
    margin-right: 8px;
  }
  .cards-item {
    width: calc((100% - 60px) / 2);
  }

  .seller-card {
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  .card-content, .card-footer {
    padding: 16px;
  }
  .card-city {
    white-space: nowrap;
  }
  .seller-phone {
    font-size: 16px;
    line-height: 20px;
  }
  .seller-row + .seller-row {
    margin-top: 24px;
  }

  .seller-contacts {
    margin-top: 24px;
  }

  .seller-email {
    font-size: 14px;
    line-height: 22px;
  }
  .a_d_s-item {
    flex-direction: column-reverse;
  }

  .a_d_s-item-seller {
    width: 100%;
    flex-direction: row;
    flex-wrap: wrap;
    margin-bottom: 32px;
    h3 {
      width: 100%;
    }
  }
  .seller-card {
    flex-direction: row;
    width: 100%;
    max-width: inherit;
  }

  .seller-image {
    flex-shrink: 0;
    margin-right: 24px;
  }

  .seller-contacts {
    width: 100%;
  }

  .seller-info {
    flex-shrink: 1;
  }

  .seller-info > *{
    text-align: left;
  }

  .seller-dept::before {
    left: 0;
    margin-left: 0;
  }

  .seller-row {
    flex-direction: row;
    align-items: baseline;
  }

  .seller-contacts {
    padding-left: 164px;
  }
  .seller-phone {
    margin-top: 0;
    margin-left: 16px;
    a {
      display: block;
    }
  }
  .seller-email {
    br {
      display: none;
    }
    a {
      margin-left: 16px;
    }
  }
}

@media (max-width: 600px) {
  .cards-list {
    margin-left: 0;
    margin-right: 0;
  }

  .cards-item {
    margin-left: 0;
    margin-right: 0;
    width: 100%;
  }
}

@media (max-width: 440px) {
  .seller-card {
    flex-direction: column;
  }
  .seller-info > * {
    text-align: center;
  }

  .seller-dept::before {
    left: 50%;
    margin-left: -35px;
  }
  .seller-contacts {
    padding-left: 0;
  }
}
